import React, { Component } from 'react';
import Markdown from '../../../components/markdown';
import Readme from './index.md';
import { fromJS, is } from 'immutable';
import './index.scss';

class Guide extends Component {
	state = {
		markdown: ''
	}

	componentWillMount() {
		fetch(Readme)
			.then(res => res.text())
			.then(text => this.setState({ markdown: text }));
	}
	
	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	render() {
		return (
			<div id="guide">
				<Markdown markdown={this.state.markdown}/>
				<h3 className="mt-20 mb-20">兼容性说明</h3>
				<p>Ant Design React 支持所有的现代浏览器和 IE11。</p>
				<table className="table mt-20 mb-20">
					<thead>
						<tr>
							<th>IE</th>
							<th>Firefox</th>
							<th>Chrome</th>
							<th>Safari</th>
							<th>Opera</th>
							<th>Electron</th>
						</tr>
					</thead>
					<tbody>
						<tr>
							<td>IE11, Edge</td>
							<td>last 2 versions</td>
							<td>last 2 versions</td>
							<td>last 2 versions</td>
							<td>last 2 versions</td>
							<td>last 2 versions</td>
						</tr>
					</tbody>
				</table>
				<p>我们对 IE9/10 提供有限度的支持，部分样式和动画在 IE9/10 下的表现会比较裸。少数组件使用到了 Flex 布局，在 IE9/10 下也会有问题。</p>
			</div>
		);
	}
}

export default Guide;
